import React, { Component } from "react";
import "./slideshow.css";
import EventCard from "./eventCard"
import legacyBricks from "../images/legacyBricks.jpg";
import bookSale1 from "../images/bookSale1.jpg";
import bookSale2 from "../images/bookSale2.jpg";
import bookSale3 from "../images/bookSale3.jpg";

import legacyBrickForm from "../files/legacy_brick_form.docx";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
const eventCard1 = (
    <EventCard
      eventImage={legacyBricks}
      title={"Legacy Brick Fundraiser"}
      information={
        "here is some information about the legacy brich fundraiser"
      }
      startDate={"Current"}
      endDate={"While Supplies Last"}
      width={"25%"}
      height={"auto"}
      content={
        <div>
          <Router>
            {" "}
            <div>
              {" "}
              <Link to={legacyBrickForm} target="_blank">
                Download
              </Link>
            </div>
          </Router>
        </div>
      }
      date={"While supplies last"}
    ></EventCard>
  );
  const eventCard2 = (
    <EventCard
      eventImage={bookSale1}
      title={"Spring Book Sale"}
      information={
        "here is some information about the book sale. This text says some stuff about the book sale. Hopefully this text will be helpful."
      }
      date={"tbd"}
      width={"25%"}
      height={"auto"}
      startDate={"May 5, 2021"}
      endDate={"May 15, 2021"}
      startTime={"10:00 am"}
      endTime={"5:00 pm"}
    ></EventCard>
  );
  const eventCard3 = (
    <EventCard
      eventImage={bookSale2}
      title={"Summer Book Sale"}
      information={
        "here is some information about the book sale. This text says some stuff about the book sale. Hopefully this text will be helpful."
      }
      date={"tbd"}
      width={"25%"}
      height={"auto"}
      startDate={"May 5, 2021"}
      endDate={"May 15, 2021"}
      startTime={"10:00 am"}
      endTime={"5:00 pm"}
    ></EventCard>
  );
  const eventCard4 = (
    <EventCard
      eventImage={bookSale3}
      title={"Fall Book Sale"}
      information={
        "here is some information about the book sale. This text says some stuff about the book sale. Hopefully this text will be helpful."
      }
      date={"tbd"}
      width={"25%"}
      height={"auto"}
      startDate={"May 5, 2021"}
      endDate={"May 15, 2021"}
      startTime={"10:00 am"}
      endTime={"5:00 pm"}
    ></EventCard>
  );
  const cardArray=[        
      eventCard1,
    eventCard2,
      eventCard3,
     eventCard4]
class slideshow extends Component {
  // eslint-disable-next-line no-useless-constructor
  constructor(props) {
    super(props);
    this.state = {
      slideshowImage: null,
      childrenArray:[],
      slideshowStarted: false,
    };
    this.nextSlide = this.nextSlide.bind(this);
  }
 componentDidMount(){
     this.setState({ childrenArray: this.props.children})
     console.log(this.props.children)
 }
  componentDidUpdate(prevProps) {
    if (this.prevProps !== this.props) {
      if (this.props.start && !this.state.slideshowStarted) {
        let intervalId = setInterval(this.nextSlide, 3000);
        this.setState({ slideshowStarted: true, intervalId: intervalId });
     
      }
      if (!this.props.start && this.state.slideshowStarted) {
        this.setState({ slideshowStarted: false });
        clearInterval(this.state.intervalId);
      }
    }
  }
  componentWillUnmount() {
    clearInterval(this.state.intervalId);
  }
  nextSlide() {

     let currentIndex = cardArray.indexOf(this.state.slideshowImage);
      this.setState({
        slideshowImage: cardArray[(currentIndex + 1) % 4],
      });
    


    console.log("slideshow");
  }
  render() {
    
    return (
      <div>
        {this.props.Mobile && (
          <div className="carouselParentMobile">
            {this.state.slideshowImage}
          </div>
        )}

        {!this.props.Mobile && (
          <div className="carouselParent">{this.state.slideshowImage}</div>
        )}
      </div>
    );
  }
}

export default slideshow;
