import React, { Component } from "react";
import FlipPage from "react-flip-page";
import Header from "./components/header";
import upArrow from "./images/point_arrow_up.png";
import downArrow from "./images/point_arrow_down.png";
import DonatePage from "./pages/donatePage";
import "./App.css";
import BuildingPlans from "./pages/buildingPlans";
import BuildingPlans2 from "./pages/buildingPlans2";
import Members1 from "./pages/members1";
import Members2 from "./pages/members2";
import Members from "./pages/members";
import Events from "./pages/events"
import Sponsors from "./pages/sponsors"
const UPDATED = "last update: 2/11/2021 @12:00pm (PDT)";
const ANIMATION_DURATION = 400;
const DELAY = 100;
const SCROLLFLIP = 300;
function timeout(delay) {
  return new Promise((res) => setTimeout(res, delay));
}

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      pageIndex: 0,
      scrollIndex: 0,
      width: 0,
      height: 0,
      pageFlipping: false,
      greatestHeight: 0,
    };
    this.updatePageState = this.updatePageState.bind(this);
    this.flipToPage = this.flipToPage.bind(this);
    this.upPage = this.upPage.bind(this);
    this.downPage = this.downPage.bind(this);
    this.detectScroll = this.detectScroll.bind(this);
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    this.pageFlipStarted = this.pageFlipStarted.bind(this);
  }

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener("resize", this.updateWindowDimensions);
  }
  componentWillUnmount() {
    window.removeEventListener("resize", this.updateWindowDimensions);
  }
  updateWindowDimensions() {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  }

  detectScroll(e) {
    e.preventDefault();
    let deltaY = e.deltaY;
    if (deltaY < -1) {
      this.setState({
        scrollIndex: this.state.scrollIndex + deltaY,
      });
    }
    if (deltaY > 1) {
      this.setState({
        scrollIndex: this.state.scrollIndex + deltaY,
      });
    }

    if (this.state.scrollIndex >= SCROLLFLIP) {
      this.upPage();
      this.setState({
        scrollIndex: 0,
      });
    }
    if (this.state.scrollIndex <= -SCROLLFLIP) {
      this.downPage();
      this.setState({
        scrollIndex: 0,
      });
    }
  }

  pageFlipStarted(e) {
    this.setState({
      pageFlipping: true,
    });
  }

  updatePageState(e) {
    this.setState({
      pageIndex: e,
      pageFlipping: false,
    });
  }
  upPage() {
    this.flipPage.gotoNextPage();
  }
  downPage() {
    this.flipPage.gotoPreviousPage();
  }
  async flipToPage(index) {
    let flips = index - this.state.pageIndex;
    //flip backwards, lower
    if (flips > 0) {
      while (flips !== 0) {
        this.flipPage.gotoNextPage();
        flips = flips - 1;
        await timeout(ANIMATION_DURATION + DELAY);
      }
    }
    //flip up fowards, higher
    if (flips < 0) {
      while (flips !== 0) {
        this.flipPage.gotoPreviousPage();
        flips = flips + 1;
        await timeout(ANIMATION_DURATION + DELAY);
      }
    }
  }
  render() {
    return (
      <div className="App" onWheel={(e) => this.detectScroll(e)}>
        <link
          rel="stylesheet"
          href="https://fonts.googleapis.com/css?family=Tangerine"
        />
        <meta
          name="viewport"
          content="width=device-width, initial-scale=1, minimum-scale=1, maximum-scale=1"
        ></meta>

        <body style={{ height: this.state.height }}>
          <Header FlipToPage={this.flipToPage} ScreenWidth={this.state.width} />
          {this.state.width > 800 && (
            <div
              className="flipPage"
              style={{ height: this.state.height - 50 + "px" }}
            >
              <FlipPage
                responsive={true}
                uncutPages={false}
                animationDuration={ANIMATION_DURATION}
                showTouchHint={true}
                showHint={true}
                showSwipeHint={true}
                loopForever={true}
                style={{ zIndex: 0 }}
                onPageChange={(e) => {
                  this.updatePageState(e);
                }}
                ref={(component) => {
                  this.flipPage = component;
                }}
              >
                <div
                  className="topPage"
                  style={{ height: this.state.height - 50 + "px" }}
                >
                  <div
                    alt="image3d"
                    className="drawing3d"
                    style={{ height: this.state.height - 50 + "px" }}
                  >
                    <div className="homeTextParent">
                      <h1 className="homeHeading">Our Mission</h1>
                      <p className="missionStatement">
                      The Friends of Union Gap Library and Community Center is
                        a public nonprofit organization formed in 2019 to raise
                        private funds for a new library, collections, programs
                        and services for the Union Gap Library and Community
                        Center. After construction of the new library facility,
                        the ongoing mission of the Friends of Union Gap Library
                        and Community Center is to raise private funds to
                        supplement public funding of the Union Gap Library and
                        Community Center in order to ensure that our library
                        serve the Union Gap public with free and convenient
                        facilities and services. The Friends of Union Gap
                        Library and Community Center is also being formed to
                        raise funds to help supplement the construction budget
                        for the new library. With gifts from individuals,
                        businesses and charitable foundations, the Friends of
                        Union Gap Library and Community Center can help bridge
                        the financial gap to construct the Union Gap Library and
                        Community Center.
                      </p>
                
                      <button
                      className="donateButton"
                      onClick={() => {
                        this.flipToPage(1);
                      }}
                    >
                      Donate
                    </button>
                    </div>
                
            
                  </div>
                </div>
                <DonatePage Mobile={false} Index={this.state.pageIndex} />
                <BuildingPlans
                  style={{ height: this.state.height - 50 + "px" }}
                  Mobile={false}
                  Index={this.state.pageIndex}
                />
                <Events/>
                <Members
                  style={{ height: this.state.height - 50 + "px" }}
                  Mobile={false}
                />
                <Sponsors Mobile={false}></Sponsors>
              </FlipPage>
            </div>
          )}
          {this.state.width <= 800 && (
            <div
              className="flipPage"
              style={{ height: this.state.height - 50 + "px" }}
            >
              <FlipPage
                responsive={true}
                uncutPages={false}
                animationDuration={ANIMATION_DURATION}
                showTouchHint={true}
                showHint={true}
                showSwipeHint={true}
                loopForever={true}
                style={{ zIndex: 0 }}
                onPageChange={(e) => {
                  this.updatePageState(e);
                }}
                ref={(component) => {
                  this.flipPage = component;
                }}
              >
                <div
                  className="topPage"
                  style={{ height: this.state.height - 50 + "px" }}
                >
                  <div
                    alt="image3d"
                    className="drawing3dMobile"
                    style={{ height: this.state.height - 50 + "px" }}
                  >
                    
                    <div className="homeTextParentMobile">
                    <h1 className="homeHeading">Our Mission</h1>
                      <p className="missionStatement" style={{fontSize: '16px'}}>
                        The Friends of Union Gap Library and Community Center is
                        a public nonprofit organization formed in 2019 to raise
                        private funds for a new library, collections, programs
                        and services for the Union Gap Library and Community
                        Center. 
                      </p>   <button
                      className="donateButton"
                      onClick={() => {
                        this.flipToPage(1);
                      }}
                    >
                      Donate
                    </button>
                    </div>
                  
                  </div>
                </div>
                <DonatePage
                  width={this.state.width}
                  height={this.state.height}
                  Mobile={true}
                  Index={this.state.pageIndex}
                />
                <BuildingPlans
                  width={this.state.width}
                  height={this.state.height}
                  Mobile={true}
                  Index={this.state.pageIndex}
                />
                <BuildingPlans2
                  width={this.state.width}
                  height={this.state.height}
                  Mobile={true}
                />
                <Members1
                  width={this.state.width}
                  height={this.state.height}
                  Mobile={true}
                />
                <Members2
                  width={this.state.width}
                  height={this.state.height}
                  Mobile={true}
                />
                <Sponsors Mobile={true}></Sponsors>
              </FlipPage>
            </div>
          )}

          <div className="arrowContainer">
            <input
              alt="upArrow"
              type="image"
              src={upArrow}
              className="arrow"
              onClick={() => this.downPage()}
            />
            <text className="pageNumber"> {this.state.pageIndex + 1}</text>
            <input
              alt="upArrow"
              type="image"
              src={downArrow}
              className="arrow"
              onClick={() => this.upPage()}
            />
          </div>
        </body>
      </div>
    );
  }
}

export default App;
