import React, { Component } from "react";
import "./events.css";

import Slideshow from "../components/slideshow"

import EventCard from "../components/eventCard"
import legacyBricks from "../images/legacyBricks.jpg";
import bookSale1 from "../images/bookSale1.jpg";
import bookSale2 from "../images/bookSale2.jpg";
import bookSale3 from "../images/bookSale3.jpg";
import legacyBrickForm from "../files/legacy_brick_form.docx";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
const eventCard1 = (
  <EventCard
      eventImage={legacyBricks}
      title={"Legacy Brick Fundraiser"}
      information={
        "here is some information about the legacy brich fundraiser"
      }
      startDate={"Current"}
      endDate={"While Supplies Last"}
      width={"25%"}
      height={"auto"}
      content={
        <div>
          <Router>
            {" "}
            <div>
              {" "}
              <Link to={legacyBrickForm} target="_blank">
                Download
              </Link>
            </div>
          </Router>
        </div>
      }
      date={"While supplies last"}
    ></EventCard>
  );
    
  const eventCard2 = (
    <EventCard
      eventImage={bookSale1}
      title={"Spring Book Sale"}
      information={
        "here is some information about the book sale. This text says some stuff about the book sale. Hopefully this text will be helpful."
      }
      date={"tbd"}
      width={"25%"}
      height={"auto"}
      startDate={"May 5, 2021"}
      endDate={"May 15, 2021"}
      startTime={"10:00 am"}
      endTime={"5:00 pm"}
    ></EventCard>
  );
  const eventCard3 = (
    <EventCard
      eventImage={bookSale2}
      title={"Summer Book Sale"}
      information={
        "here is some information about the book sale. This text says some stuff about the book sale. Hopefully this text will be helpful."
      }
      date={"tbd"}
      width={"25%"}
      height={"auto"}
      startDate={"May 5, 2021"}
      endDate={"May 15, 2021"}
      startTime={"10:00 am"}
      endTime={"5:00 pm"}
    ></EventCard>
  );
  const eventCard4 = (
    <EventCard
      eventImage={bookSale3}
      title={"Fall Book Sale"}
      information={
        "here is some information about the book sale. This text says some stuff about the book sale. Hopefully this text will be helpful."
      }
      date={"tbd"}
      width={"25%"}
      height={"auto"}
      startDate={"May 5, 2021"}
      endDate={"May 15, 2021"}
      startTime={"10:00 am"}
      endTime={"5:00 pm"}
    ></EventCard>
  );

class events extends Component {
  constructor(props) {
    super(props);
    this.state = {

    };

    
  }

  componentDidUpdate(prevProps, prevState) {}
  componentDidMount(){
    
  }
  componentWillUnmount() {}

  render() {
    
    return (
      <div className="eventsParent">
        <div className="">
            {this.props.mobile && <Slideshow start={true}>  </Slideshow>}
            {!this.props.mobile &&      (<div className="eventCardContainer">{eventCard1} {eventCard2} {eventCard3} {eventCard4}</div>)}

        </div>
      </div>
    );
  }
}

export default events;
